<template>
    <scroll 
        :pullup="pullup" 
        class="suggest" 
        :data="result"
        :beforeScroll="beforeScroll"
        @beforeScroll="listScroll"
        @scrollToEnd="searchMore"
        ref="suggest">
        <ul class="suggest-list">
            <li @click="selectItem(item)" v-for="item in result" class="suggest-item">
                <div class="icon">
                    <i :class="getIconCls(item)"></i>
                </div>
                <div class="name">
                    <p class="text" v-html="getDisplayName(item)"></p>
                </div>
            </li>
            <loading v-show="hasMore" title=""></loading>
        </ul>
        <div class="no-result-wrapper">
            <no-result v-show="!hasMore && !result.length" title="抱歉，暂无搜索结果"></no-result>
        </div>
    </scroll>
</template>

<script type="text/javascript">
    import {search} from '../../api/search.js'
    import {ERR_OK} from '../../api/config.js'
    import {createSong} from '../../common/js/song.js'
    import Scroll from '../../base/scroll/scroll.vue'
    import Loading from '../../base/loading/loading.vue'
    import Singer from '../../common/js/singer.js'
    import {mapMutations,mapActions} from 'vuex'
    import NoResult from '../../base/no-result/no-result.vue'

    const TYPE_SINGER = 'singer'
    const perpage = 20

    export default {
        props: {
            query: {
                type: String,
                default: ''
            },
            showSinger: {
                type: Boolean,
                default: true
            }
        },
        data(){
            return {
                page: 1,
                result: [],
                pullup: true,
                hasMore: true,
                beforeScroll: true
            }
        },
        methods:{
            //请求服务端抓取数据
            search(){
                this.page = 1
                this.hasMore = true
                this.$refs.suggest.scrollTo(0,0)
                search(this.query,this.page,this.showSinger,perpage)
                    .then((res) => {
                        if(res.code === ERR_OK){
                            this.result = this._genResult(res.data)
                            this._checkMore(res.data)
                        }
                    })
            },
            searchMore(){
                if(!this.hasMore){
                    return
                }
                this.page++
                search(this.query, this.page, this.showSinger, perpage).then((res) => {
                    this.result = this.result.concat(this._genResult(res.data))
                    this._checkMore(res.data)
                })
            },
            //根据搜索结果（歌手、歌曲）展示不同icon
            getIconCls(item){
                if(item.type === TYPE_SINGER){
                    return 'icon-mine'
                }else{
                    return 'icon-music'
                }
            },
            getDisplayName(item){
                if(item.type === TYPE_SINGER){
                    return item.singername
                }else{
                    return `${item.name}-${item.singer}`
                }
            },
            selectItem(item){
                if(item.type === TYPE_SINGER){
                    const singer = new Singer({
                        id: item.singermid,
                        name: item.singername
                    })
                    this.$router.push({
                        path: `/search/${singer.id}`
                    })
                    this.setSinger(singer)
                }else{
                    this.insertSong(item)
                }

                this.$emit('select',item)
            },
            refresh(){
                this.$refs.suggest.refresh()
            },
            listScroll(){
                this.$emit('listScroll')
            },
            _checkMore(data){
                const song = data.song
                if(!song.list.length || (song.curnum + song.curpage * perpage) > song.totalnum){
                    this.hasMore = false
                }
            },
            _genResult(data) {
                let ret = []
                if(data.zhida && data.zhida.singerid) {
                    ret.push({...data.zhida,...{type: TYPE_SINGER}})
                }
                if(data.song){
                    ret = ret.concat(this._normalizeSongs(data.song.list))
                }
                return ret
            },
            _normalizeSongs(list){
                let ret = []
                list.forEach((musicData) => {
                    if(musicData.songid && musicData.albumid){
                        ret.push(createSong(musicData))
                    }
                })
                return ret
            },
            ...mapMutations({
                setSinger: 'SET_SINGER'
            }),
            ...mapActions([
                'insertSong'
            ])
        },
        components: {
            Scroll,
            Loading,
            NoResult
        },
        watch: {
            //input框数据发生变化就开始搜索
            query() {
                this.search()
            }
        }
    }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: white
      .name
        flex: 1
        font-size: $font-size-medium
        color: white
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


